import React, { Component } from "react";
import { ThunkDispatch } from "redux-thunk";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import {
  createStyles,
  Grid,
  Theme,
  Typography,
  WithStyles,
  withStyles,
} from "@material-ui/core";

import {
  requestPlayer,
  resetPlayer,
  changeMode,
  resizePlayer,
} from "../../../_actions/player.actions";
import { requestExercise } from "../../../_actions/exercise.actions";
import { ICardDetail, AppActions, AppState } from "../../../_types";
import NoteCanvas from "../../Layout/Instruments/Piano/NoteCanvas";
import { PlayerLeftSide } from "../../Layout/Player/PlayerSides";
import Fingers from "../../Layout/Instruments/Finger/Fingers";
import { IMelodition } from "../../../_types/player.types";
import { pathFinder } from "../../../_helpers/constants";
import { PlayButton } from "../../Layout/Player/Buttons";
import { LoadingCircle } from "../../Layout/Loadings";
import Piano from "../../Layout/Instruments/Piano";

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      width: "100vw",
      minHeight: "calc(100vh - 128px)",
      overflow: "hidden",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 113px)",
      },
      display: "flex",
    },
    autoMargin: {
      margin: "auto",
    },
    main: {
      width: "100vw",
      minHeight: "calc(100vh - 128px)",
      overflow: "hidden",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 113px)",
      },
      display: "flex",
    },
    content: {
      height: "100%",
      width: "100%",
      flexWrap: "nowrap",
    },
    doubleGrid: {
      flexWrap: "nowrap",
      width: "100%",
      maxWidth: "100%",
      "& > div": {
        margin: "auto",
        padding: 0,
        height: "100%",
      },
    },
    centerText: {
      textAlign: "center",
    },
    sideStyle: {
      height: "100%",
      width: "100%",
      flexWrap: "nowrap",
    },
    loading: {
      display: "flex",
      height: "200px",
      boxShadow: "inset 0 1px 4px 0px black",
    },
  });

interface LessonProps extends WithStyles<typeof useStyles> {
  error: string;
  data?: ICardDetail;
  pathname: string;
  melodition: IMelodition;
  theme: "light" | "dark";
}

interface States {}

type Props = LessonProps & ReturnType<typeof mapDispatchToProps>;

class Exercise extends Component<Props, States> {
  shouldComponentUpdate(nextProps: Props) {
    if (nextProps.data && this.props.data?.id !== nextProps.data.id) {
      this.props.requestPlayer(nextProps.data);
    }
    return true;
  }
  componentDidMount() {
    console.log("mounted");
    window.addEventListener("resize", this.updateDimensions);
    const path = pathFinder(this.props.pathname);
    this.props.requestLesson(path);
    if (this.props.data && this.props.data.id === path.exercise) {
      this.props.requestPlayer(this.props.data);
    }
  }
  componentWillUnmount() {
    console.log("dismount");
    this.props.resetPlayer();
    window.removeEventListener("resize", this.updateDimensions);
    this.setState = () => {
      return;
    };
  }
  updateDimensions = () => {
    if (!this.props.melodition.pianoLoading) {
      this.props.resizePlayer();
    }
  };

  svgWidth = (i: number): number => {
    const num = i === 1 ? 668 : 584;
    return num;
  };

  playModeChanger = () => {
    this.props.changeMode(true);
  };
  render() {
    const { classes, data, melodition, error, ...rest } = this.props;
    return (
      <div className={classes.root}>
        {melodition.loading || melodition.playMode ? (
          <LoadingCircle />
        ) : data ? (
          <Grid container className={classes.main}>
            <Grid item xs={1}>
              <PlayerLeftSide {...rest} data={data} />
            </Grid>
            <Grid item xs={10}>
              <Grid
                container
                direction="column"
                justify="center"
                alignItems="center"
                className={classes.content}
              >
                <Grid item xs={6} className={classes.doubleGrid}>
                  <Grid container justify="center" alignItems="center">
                    <Grid item xs={12}>
                      {melodition.pianoLoading ? (
                        <LoadingCircle noPad />
                      ) : (
                        <NoteCanvas />
                      )}
                      <Piano />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={3} className={classes.doubleGrid}>
                  <Grid container justify="center" alignItems="center">
                    <Grid item xs={6}>
                      <Typography>{data.description}</Typography>
                    </Grid>
                    <Grid item xs={6} className={classes.centerText}>
                      <Fingers />
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={1}>
              <Grid
                container
                direction="column"
                justify="center"
                alignItems="center"
                className={classes.sideStyle}
              >
                <PlayButton loading={melodition.loading} />
              </Grid>
            </Grid>
          </Grid>
        ) : (
          <Typography component="div" className={classes.autoMargin}>
            {error}
          </Typography>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state: AppState, ownProps: Props) => ({
  ...state.single.exercise,
  melodition: state.melodition,
  theme: state.auth.theme,
  classes: ownProps.classes,
  course: state.single.course.data,
  pathname: state.router.location.pathname,
});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  changeMode: bindActionCreators(changeMode, dispatch),
  requestLesson: bindActionCreators(requestExercise, dispatch),
  requestPlayer: bindActionCreators(requestPlayer, dispatch),
  resetPlayer: bindActionCreators(resetPlayer, dispatch),
  resizePlayer: bindActionCreators(resizePlayer, dispatch),
});

const connectedExercise = connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(Exercise));

export default connectedExercise;
