import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { drawNotes } from "../../../../../_actions/player.actions";
import { IMelodition } from "../../../../../_types/player.types";
import { AppActions, AppState } from "../../../../../_types";
import { LoadingCircle } from "../../../Loadings";
import NoteBack from "./NoteBack";

interface Props {
  Melo: IMelodition;
  drawNotes: (canvas: any) => void;
}
const NoteCanvas: React.FC<Props> = ({ Melo, drawNotes }) => {
  const drawFrontContext = (ref: HTMLCanvasElement | null) => {
    if (ref && Melo.notesLoading) {
      drawNotes(ref);
    }
  };
  if (Melo.noteInfo) {
    const { width, height } = Melo.noteInfo;
    return (
      <div
        style={{
          width: width,
          height: height,
          position: "relative",
          margin: "5px 0",
        }}
      >
        <NoteBack {...Melo.noteInfo} loading={Melo.notesLoading} />
        <canvas
          ref={(ref) => drawFrontContext(ref)}
          width={width}
          height={height}
          style={{ position: "absolute", top: 0, left: 0, zIndex: 2 }}
        />
      </div>
    );
  } else return <LoadingCircle />;
};

const mapStateToProps = (state: AppState) => ({
  Melo: state.melodition,
});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  drawNotes: bindActionCreators(drawNotes, dispatch),
});

const connectedPianoCanvas = connect(
  mapStateToProps,
  mapDispatchToProps
)(NoteCanvas);

export default connectedPianoCanvas;
