import React from "react";
import { pitchName, outLine } from "../constant";
import { INoteDraw } from "../../../../../_types/player.types";

interface CanvasProps {
  loading: boolean;
}
const NoteBack: React.FC<CanvasProps & INoteDraw> = (props) => {
  const { width, height, loading, ...Melo } = props;
  const drawBackContext = (ref: HTMLCanvasElement | null) => {
    if (ref && loading) {
      const backCtx = ref.getContext("2d")!;
      backCtx.clearRect(0, 0, width, height);
      backCtx.strokeStyle = "black";
      backCtx.lineWidth = 0.1;
      backCtx.strokeRect(0, Melo.tapSize / 2, width, height - Melo.tapSize);
      backCtx.fillStyle = "rgba(200, 200, 200, 0.2)";
      backCtx.fillRect(0, Melo.tapSize / 2, width, height - Melo.tapSize);
      Melo.linesArray.forEach((v, i) => {
        const y = ((i + 1) * Melo.tapSize) / 2;
        if (i % 2 !== 0 && i !== Melo.linesArray.length - 1) {
          backCtx.beginPath();
          backCtx.font = "bold " + Melo.fontSize + "px arial";
          backCtx.textAlign = "center";
          backCtx.textBaseline = "middle";
          backCtx.fillStyle = "black";
          backCtx.fillText(pitchName(v), Melo.spaceSize * 0.5, y);
          if (!outLine(v)) {
            backCtx.moveTo(Melo.spaceSize, y);
            backCtx.lineTo(width - Melo.spaceSize / 2, y);
            backCtx.lineWidth = 1.5;
            backCtx.strokeStyle = "rgba(50, 50, 50, 1)";
            backCtx.stroke();
            backCtx.fill();
          }
        }
      });
    }
  };
  return (
    <canvas
      ref={(ref) => drawBackContext(ref)}
      width={width}
      height={height}
      style={{ zIndex: 1 }}
    />
  );
};
export default NoteBack;
